// let startY, moveY, touchending;
export default {
	props: {
		top: {
			// 距离顶部距离, 单位upx
			type: Number,
			default: 0
		}
	},
	data(){
			return {
				pageTransition: 0,
				pageDeviation: 0,
				startY: '',
				moveY: '',
				touchending: false,
				timeDiff: 0
			}
	},
	computed: {
		pageTop() {
			return uni.upx2px(this.top);
		}
	},
	methods: {
		handleTouchstart(e) {
			// console.log(e.touches[0].pageY);
			this.touchending = false;
			this.pageTransition = 0;
			this.startY = e.touches[0].pageY;
		},
		handleTouchmove(e) {
			if(this.touchending){
				return;
			}
			// console.log(e);
			this.moveY = e.touches[0].pageY - this.startY;
			
			if(this.moveY >= 0){
				this.pageDeviation = this.moveY;
			}
		},
		handleTouchend(e) {
			this.touchending = true;
			
			if(this.moveY == 0){
				return;
			}
			
			this.pageTransition = 0.3;
			
			if(this.moveY >= 50){
				this.startPullDownRefresh();
			}else {
				this.pageDeviation = 0;
				this.startY = this.moveY = 0;
			}
		},
		startPullDownRefresh(){
			if(+new Date() - this.timeDiff < 100){
				return;
			}
			this.timeDiff = +new Date();
			this.pageDeviation = uni.upx2px(96);
			this.$emit("refresh");
		},
		endPulldownRefresh(){
			this.pageDeviation = uni.upx2px(0);
		}
	}
}
